import { Component, OnInit, Input } from '@angular/core';
import { trigger, state, style, transition, animate, keyframes } from '@angular/animations';
import { LayoutService } from '../../services/layout.service';

@Component({
	selector: 'app-work-card',
	templateUrl: './work-card.component.html',
	styleUrls: ['./work-card.component.scss'],
	animations: [
		trigger('cardAnimation', [
			state('small', style({
				transform: 'scale(1)',
			})),
			state('large', style({
				transform: 'scale(1.02)',
			})),
			transition('small => large', animate('400ms ease')),
			transition('large => small', animate('400ms ease')),
			state('hide', style({
				opacity: 0
			})),
			state('show', style({
				opacity: 1
			})),
			transition('hide => show', animate('400ms ease'))
		]),
	]
})
export class WorkCardComponent implements OnInit {

	private page: {
		style: {
			"background-image": string;
			"margin": string;
		}
	};

	private state: string = "hide";

	@Input() private link: {
		description: string,
		url: string,
		name: string,
		background: string
	};

	@Input() private delay: number;

	constructor(private layoutService: LayoutService) {
		this.page = {
			style: {
				"background-image": "url()",
				"margin": "0 1em"
			}
		};
	}

	ngOnInit() {
		this.drawLayout();
		this.page.style["background-image"] = "url(" + this.link.background + ")";
		setTimeout(() => {
			this.state = "show";
			setTimeout(() => {
				this.state = "small";
			}, 400);
		}, this.delay);
	}

	private drawLayout = function() {
		this.layoutService.dimension((width, height) => {
			// console.log(width, height);
			if(width < 750) {
				this.page.style["margin"] = "0";
			} else {
				this.page.style["margin"] = "0 1em";
			}
		});
	}

	private zoom = function(action) {
		if(this.state != "hide" && this.state != "show") {
			if(action == "in") {
				this.state = "small";
			} else if(action == "out") {
				this.state = "large";
			}
		}
	}

}



