import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { LanguageService } from '../../services/language.service';
import { LayoutService } from '../../services/layout.service';

@Component({
	selector: 'app-work-page',
	templateUrl: './work-page.component.html',
	styleUrls: ['./work-page.component.scss']
})
export class WorkPageComponent implements OnInit {

	private language: any;
	private sub: any;

	// private masonry: {
	// 	items: Array<{
	// 		url: string
	// 	}>,
	// 	options: any,
	// 	useAnimation: boolean,
	// 	useImagesLoaded: boolean,
	// 	scrollAnimationOptions: any
	// };

	private projects: Array<{
		url: string,
		name: string,
		link: string
	}>;

	private page: {
		cols: number,
		colspan: number,
		style: {
			"min-height": string
		}
	};

	constructor(private route: ActivatedRoute, private layoutService: LayoutService, private languageService: LanguageService) {
		this.language = this.languageService.loadLanguage();
		// this.masonry = {
		// 	items: [],
		// 	options: {
		// 		transitionDuration: '0.8s',
		// 		gutter: 5
		// 	},
		// 	useAnimation: true,
		// 	useImagesLoaded: true,
		// 	scrollAnimationOptions: {
		// 		animationEffect: 'effect-4',
		// 		minDuration : 0.4,
		// 		maxDuration : 0.7
		// 	}
		// };
		this.projects = [];

		this.page = {
			cols: 4,
			colspan: 1,
			style: {
				"min-height": "initial"
			}
		};
	}

	ngOnInit() {
		this.drawLayout();
		this.sub = this.route.params.subscribe((params) => {
			console.log("work", params.id);
			switch(params.id) {
				case "commercial":
					this.projects = [{
						url: "/assets/commercial/wuhan_ipsc/image_01.jpg",
						name: "Wuhan International Plaza Shopping Centre (China)",
						link: "/galleries/wuhan_ipsc"
					}, {
						url: "/assets/commercial/xian_cgzjts/image_01.jpg",
						name: "Xian Century Ginwa Zhu Jiang Time Square (China)",
						link: "/galleries/xian_cgzjts"
					}, {
						url: "/assets/commercial/suzhou_mxgtd/image_01.jpg",
						name: "Suzhou Matro Xin Gang Tian Du (China)",
						link: "/galleries/suzhou_mxgtd"
					}];
				break;
				case "residential":
					this.projects = [{
						url: "/assets/residential/singapore_rp/image_01.jpg",
						name: "Ridley Park (Singapore)",
						link: "/galleries/singapore_rp"
					}]
				break;
				case "hotel":
					this.projects = [{
						url: "/assets/hotel/myanmar_ptd/image_01.jpg",
						name: "Pray Towers Development (Myanmar)",
						link: "/galleries/myanmar_ptd"
					}]
				break;
				case "office":
					this.projects = []
				break;
			}
		});
	}

	ngOnDestroy() {
		this.sub.unsubscribe();
	}

	private drawLayout = function() {
		this.layoutService.dimension((width, height) => {
			// console.log(width, height);
			if(width < 700) {
				this.page.cols = 1;
				this.page.colspan = 1;
			} else if(width < 900) {
				this.page.cols = 2;
				this.page.colspan = 1;
			} else if(width < 1049) {
				this.page.cols = 3;
				this.page.colspan = 1;
			} else {
				this.page.cols = 4;
				this.page.colspan = 1;
			}
			this.page.style["min-height"] = height - 138 + "px";
		});
	};

}



