import { Injectable } from '@angular/core';
import * as L from '../resources/language';

@Injectable()
export class LanguageService {

	private language: any;

	constructor() {
		this.language = L.Language.en;
	}

	public loadLanguage = function() {
		let lan = localStorage.getItem("CATHY_LANGUAGE");
		if(!lan) {
			lan = "EN";
			localStorage.setItem("CATHY_LANGUAGE", lan);
		}
		if(lan == "EN") {
			return L.Language.en;
		} else if(lan == "CH") {
			return L.Language.ch;
		}
	};

	public getLanguage = function() {
		let lan = localStorage.getItem("CATHY_LANGUAGE");
		if(!lan) {
			lan = "EN";
		}
		return lan;
	};

	private setLanguage = function(state: string) {
		localStorage.setItem("CATHY_LANGUAGE", state);
	};

}



